import React from "react";
import PropTypes from "prop-types";
import { FaSave as IconSave } from "react-icons/fa";
import ToolbarButton from "./toolbar-button";
import { browserDownload } from "../../utils/browser";
import { Project } from "../../class/export";
import axios from "axios";
import toast from "react-hot-toast";
import { APIURL } from "../../../demo/src/APIURL";

export default function ToolbarSaveButton({ state }, { translator }) {
  const urlParams = new URLSearchParams(window.location.search);
  const restaurant = urlParams.get("restaurant");
  // const domain = urlParams.get("domain");
  console.log("restaurant", restaurant);

  const axiosInstance = axios.create({
    baseURL: `${APIURL}/api/v1/restaurant/planner`,
  });

  const saveProject = () => {};

  let saveProjectToFile = (e) => {
    e.preventDefault();
    state = Project.unselectAll(state).updatedState;

    const formData = new FormData();
    const jsonData = JSON.stringify(state.get("scene").toJS());
    const blob = new Blob([jsonData], {
      type: "application/json",
    });
    formData.append("file", blob, "data.json");
    const url = URL.createObjectURL(blob);

    axiosInstance
      .post(
        `/save`,
        {
          restaurant_id: restaurant,
          file: blob,
          // file: JSON.stringify(state.get("scene").toJS()),
        },
        {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        }
      )
      .then((res) => {
        console.log("res", res);
        toast.success("Saved successfully");
      });
    // browserDownload(state.get("scene").toJS());
    console.log("jsss", state.get("scene").toJS());
  };

  return (
    <ToolbarButton
      active={false}
      tooltip={translator.t("Save project")}
      onClick={saveProjectToFile}
    >
      <IconSave />
    </ToolbarButton>
  );
}

ToolbarSaveButton.propTypes = {
  state: PropTypes.object.isRequired,
};

ToolbarSaveButton.contextTypes = {
  translator: PropTypes.object.isRequired,
};
